*{
margin: 0;
padding: 0;
#clock{
    position: relative;
    left: 120px;
    top: 50px;
    margin: 0 auto;
    width: 400px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size:48px;
    color: black;

}
#clock :hover{
    cursor: pointer;
    transform: rotateY(180deg);
    transition: transform 0.5s ease
}
.mainIntroduce{
    position: absolute;
    border:1px solid darkgrey;
    width: 500px;
    top: 52%;
    right:34%;
    padding: 10px;
    // translate: scale(50%,50%);
    border-radius:20px
}

}